<i18n>
{
  "en": {
    "MyWallet": "My wallet",
    "Detail": "Detail",
    "balanceOfAccounts": "The balance of accounts",
    "unit": "￥",
    "Recharge": "Recharge",
    "Deposit": "Deposit",
    "Paid": "Paid",
    "UnpaidDeposit": "Unpaid deposit",
    "Withdrawal": "Withdrawal"
  },
  "zh": {
    "MyWallet": "我的钱包",
    "Detail": "明细",
    "balanceOfAccounts": "余额账户",
    "unit": "元",
    "Recharge": "充值",
    "Deposit": "押金",
    "Paid": "已交押金",
    "UnpaidDeposit": "未交押金",
    "Withdrawal": "提现"
  }
}
</i18n>

<template>
  <div class="wallet">
    <head-top :title='$t("MyWallet")' :go-back="true" @goBackHander="goBackHander">
      <button slot="setRight" class="transaction" @click="itemHandle(1)">{{ $t("Detail") }}</button>
    </head-top>
    <div class="container">
      <div class="column">
        <img src="../../assets/wallet_images/bag.png" alt="" class="bag">
        <div class="balance">
          <p class="tips">{{ $t("balanceOfAccounts") }}</p>
          <p class="count">
            <span>
              {{amount / 100}}
              <strong>{{ $t("unit") }}</strong>
            </span>
          </p>
        </div>
        <button @click="itemHandle(2)">{{ $t("Recharge") }}</button>
      </div>
      <div class="column" @click="itemHandle(3)">
        <div class="left">{{ $t("Deposit") }}</div>
        <div class="right">
          <span v-if="deposit">{{ $t("Paid") }}</span>
          <span class="red" v-else>{{ $t("UnpaidDeposit") }}</span>
          <img src="../../assets/global_images/next.png" alt="">
        </div>
      </div>
      <div :class="['column', amount? 'active': '']" @click="itemHandle(4)">
        {{ $t("Withdrawal") }}
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import headTop from '../../components/Header/index'
export default {
  name: 'wallet',
  data () {
    return {
      deposit: 0, // 押金
      amount: 0,
      depositStandard: 0 // 应收押金
    }
  },

  components: {
    headTop
  },

  mounted () {
    this.dataInteractionResponse(res => {
      // console.log(res)
      switch (res.msgType) {
        // 查询押金及余额返回
        case '0x5242':
          if (res.result && res.data) {
            this.deposit = res.data.deposit
            this.amount = res.data.amount
          }
          break
        case '0x5246':
          if (res.result && res.data) {
            if (res.data.isDeposit === 1) {
              this.$router.push({
                name: 'deposit',
                params: {
                  deposit: this.deposit,
                  depositStandard: res.data.depFee
                }
              })
            } else {
              this.setGlobalToast({
                bol: true,
                zh: '不需要交押金喔~',
                en: 'No deposit required!'
              })
            }
          }
          break
      }
    })
    // 查询押金及余额请求
    this.dataInteractionRequest({
      msgType: '0x5241'
    })
  },

  methods: {
    ...mapActions([
      'setGlobalToast'
    ]),
    goBackHander () {
      // console.log(123)
    },
    itemHandle (num) {
      switch (num) {
        case 1:
          this.$router.push({'path': '/transaction'})
          break
        case 2:
          this.$router.push({'path': '/recharge'})
          break
        case 3:
          if (this.deposit === 0) {
            // 查询收费标准请求
            this.dataInteractionRequest({
              msgType: '0x5245'
            })
            if (this.ISDEBUG) {
              let str = '{"msgType":"0x5246","result":true,"data":{"id":"2","depFee":0,"isDeposit":2,"remark":"免押金"}}'
              window.dataInteractionResponse(str)
            }
          } else {
            // 进入退押金界面
            // this.$router.push({
            //   name: 'deposit',
            //   params: {
            //     deposit: this.deposit,
            //     depositStandard: this.deposit
            //   }
            // })
          }
          break
        case 4:
          // if (!this.amount) {
          //   return false
          // }
          // this.$router.push({'path': '/withdraw'})
          break
      }
    },
    withdraw () {
      if (!this.amount) {
        return false
      }
      this.$router.push({'path': '/withdraw'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.wallet{
  .fs;
  background: #FFF;
  .transaction{
    background: @statusBarColor;
    .wh(100px,100%);
    float: right;
    margin-right: 10%;
  }
  .container{
    .fs-noTop;
    .column{
      .wh(690px, 480px);
      margin: 30px auto 0 auto;
      position: relative;
      border-radius: 20px;
      box-shadow: 0px 4px 25px rgba(130,130,130,0.2); // 左上下右
      .bag{
        height: 160px;
        margin-top: 30px;
      }
      .balance{
        padding-top: 30px;
        .tips{
          color: #999999;
          font-size: 30px;
        }
        .count{
          font-size: 30px;
          span{
            font-size: 80px;
            font-family: "Helvetica Neue";
            position: relative;
            strong{
              position: absolute;
              bottom: 10px;
              right: -70px;
              font-size: 30px;
            }
          }
        }
      }
      button{
        .wh-l(480px, 80px);
        background: @themeColor;
        color: #585858;
        position: absolute;
        left: 50%;
        bottom: -40px;
        margin-left: -240px;
        border-radius: 40px;
        font-size: 30px;
      }
    }
    .column:nth-child(2){
      height: 90px;
      line-height: 90px;
      margin-top: 100px;
      display: flex;
      .left{
        .wh(40%,100%);
        line-height: 100px;
        padding-left: 33px;
        text-align: left;
        font-size: 30px;
      }
      .right{
        flex: 1;
        height: 100%;
        line-height: 100px;
        text-align: right;
        padding-right: 30px;
        position: relative;
        span{
          .wh(auto, 100%);
          line-height: 90px;
          color:#cccccc;
          font-size: 26px;
          padding-right: 33px;
          display: block;
          &.red{
            color: red;
          }
        }
        img{
          height: 26px;
          position: absolute;
          top: 50%;
          right: 30px;
          margin-top: -13px;
        }
      }
    }
    .column:nth-child(3){
      .wh-l(690px,100px);
      font-size: 30px;
      background: #aaaaaa;
      color: #e4e4e4;
      position: absolute;
      left: 50%;
      margin-left: -345px;
      bottom: 50px;
      border-radius: 10px;
      &.active{
        background: #f4da21;
        color: #585858;
      }
    }
  }
}
</style>



